function storeValue(storage, path, value) {
    let obj = {};
    const data = storage.getItem('digi_edit:storage');
    if (data) {
        obj = JSON.parse(data);
    }
    const pathElements = path.split('.');
    let current = obj;
    for (let idx = 0; idx < pathElements.length; idx++) {
        const element = pathElements[idx];
        if (idx === pathElements.length - 1) {
            current[element] = value;
        }
        else {
            if (!current[element]) {
                current[element] = {};
            }
            current = current[element];
        }
    }
    storage.setItem('digi_edit:storage', JSON.stringify(obj));
}
function loadValue(storage, path, defaultValue) {
    const data = storage.getItem('digi_edit:storage');
    if (data) {
        const obj = JSON.parse(data);
        const pathElements = path.split('.');
        let current = obj;
        for (let idx = 0; idx < pathElements.length; idx++) {
            const element = pathElements[idx];
            if (idx === pathElements.length - 1) {
                if (current[element] !== undefined) {
                    return current[element];
                }
                else {
                    return defaultValue;
                }
            }
            else {
                if (current[element]) {
                    current = current[element];
                }
                else {
                    return defaultValue;
                }
            }
        }
    }
    return defaultValue;
}
function deleteValue(storage, path) {
    let obj = {};
    const data = storage.getItem('digi_edit:storage');
    if (data) {
        obj = JSON.parse(data);
    }
    const pathElements = path.split('.');
    let current = obj;
    for (let idx = 0; idx < pathElements.length; idx++) {
        const element = pathElements[idx];
        if (idx === pathElements.length - 1) {
            delete current[element];
        }
        else {
            if (!current[element]) {
                break;
            }
            current = current[element];
        }
    }
    storage.setItem('digi_edit:storage', JSON.stringify(obj));
}
export function sessionStoreValue(path, value) {
    storeValue(sessionStorage, path, value);
}
export function sessionLoadValue(path, defaultValue) {
    return loadValue(sessionStorage, path, defaultValue);
}
export function sessionDeleteValue(path) {
    return deleteValue(sessionStorage, path);
}
export function localStoreValue(path, value) {
    storeValue(localStorage, path, value);
}
export function localLoadValue(path, defaultValue) {
    return loadValue(localStorage, path, defaultValue);
}
export function localDeleteValue(path) {
    return deleteValue(localStorage, path);
}
